@import url(https://fonts.googleapis.com/css?family=Montserrat|Nunito&display=swap);
body {
  margin: 0;
  min-height: 100vh;
  height: 100%;
  font-family: 'Nunito',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-gradient(linear, left bottom, right top, from(#2886c6), color-stop(#00a3d0), color-stop(#00bcb5), color-stop(#1fcd7f), to(#a8d543));
  background-image: linear-gradient(to right top, #2886c6, #00a3d0, #00bcb5, #1fcd7f, #a8d543);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img{
    max-width: 200px;
    max-height: 200px;
}



.heading{
    font-size: 5rem;
}

@media all and (max-width: 599px){
    .heading{
        font-size: 3.5rem;
    }
}

@media all and (max-width: 399px){
    .heading{
        font-size: 2.5rem;
    }
}

